@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

:root {
    --bg-color: #1c1c1c;
    --text-color: #ffffff;
    --accent-color: #ffd700;
    --secondary-color: #333333;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--bg-color);
    color: var(--text-color);
    line-height: 1.6;
}

/* Navigation styles */
nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: rgba(28, 28, 28, 0.8);
    backdrop-filter: blur(10px);
}

.logo {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--accent-color);
}

nav ul {
    display: flex;
    list-style: none;
}

nav ul li {
    margin-left: 2rem;
}

nav ul li a {
    color: var(--text-color);
    text-decoration: none;
    transition: color 0.3s ease;
}

nav ul li a:hover, nav ul li a.active {
    color: var(--accent-color);
}

.search {
    display: flex;
    align-items: center;
}

.search input {
    padding: 0.5rem;
    border: none;
    border-radius: 20px;
    background-color: var(--secondary-color);
    color: var(--text-color);
}

.search i {
    margin-left: -30px;
    color: var(--text-color);
}

/* Main content styles */
main {
    padding-top: 80px;
}

section {
    padding: 4rem 5%;
    min-height: 100vh;
    display: none;
}

section.active {
    display: block;
    animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

/* Hero section styles */
.hero {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.hero img {
    width: 350px;
    border-radius: 50%;
    border: 5px solid var(--accent-color);
}

.hero-text h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
}

.hero-text h1 span {
    color: var(--accent-color);
}

.hero-text p {
    font-size: 1.25rem;
    margin-bottom: 2rem;
}

.cta {
    padding: 0.8rem 2rem;
    background-color: var(--accent-color);
    color: var(--bg-color);
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.cta:hover {
    transform: translateY(-5px);
}

/* Social links styles */
.social-links {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}

.social-links a {
    color: var(--text-color);
    font-size: 1.5rem;
    margin: 0 1rem;
    transition: color 0.3s ease;
}

.social-links a:hover {
    color: var(--accent-color);
}

/* Section headers */
h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: var(--accent-color);
}

/* About section styles */
.about p {
    margin-bottom: 2rem;
}

/* Skills section styles */
.skill-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 1rem;
}

.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.skill-item:hover {
    transform: translateY(-5px);
}

.skills_box{
    max-width: 50px;
}

/* Portfolio section styles */
.portfolio-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
}

.portfolio-item {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    height: 15rem;
    width: 12rem;
}

.portfolio-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.portfolio-item:hover img {
    transform: scale(1.1);
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.portfolio-item:hover .overlay {
    opacity: 1;
}

.overlay h3 {
    color: var(--accent-color);
    margin-bottom: 1rem;
}

.btn {
    padding: 0.5rem 1rem;
    background-color: var(--accent-color);
    color: var(--bg-color);
    text-decoration: none;
    border-radius: 20px;
}

.view-more {
    display: block;
    margin: 2rem auto 0;
    padding: 0.8rem 2rem;
    background-color: var(--secondary-color);
    color: var(--text-color);
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.view-more:hover {
    background-color: var(--accent-color);
    color: var(--bg-color);
}

/* Contact section styles */
#contact {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#contact form {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;
}

#contact form input,
#contact form textarea {
    width: 100%;
    padding: 0.8rem;
    border: none;
    border-radius: 5px;
    background-color: var(--secondary-color);
    color: var(--text-color);
}

#contact form textarea {
    resize: vertical;
    min-height: 150px;
}

#contact form button {
    align-self: flex-start;
    padding: 0.8rem 2rem;
    background-color: var(--accent-color);
    color: var(--bg-color);
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: transform 0.3s ease;
}

#contact form button:hover {
    transform: translateY(-5px);
}

.contact-info {
    text-align: center;
}

.contact-info p {
    margin-bottom: 0.5rem;
}

.contact-info svg {
    margin-right: 0.5rem;
    color: var(--accent-color);
}

/* Responsive design */
@media screen and (max-width: 768px) {
    nav {
        flex-direction: row;
        padding: 1rem;
        align-items: center;
    }

    nav ul {
        margin-top: 1rem;
    }

    nav ul li {
        margin-left: 1rem;
    }

    .search {
        margin-top: 1rem;
    }

    .hero {
        flex-direction: column;
        text-align: center;
    }

    .hero img {
        margin-bottom: 2rem;
    }

    .portfolio-grid {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

    .portfolio-item {
        width: 100%;
    }
}

@media screen and (max-width: 480px) {
    .hero-text h1 {
        font-size: 2.5rem;
    }

    .hero-text p {
        font-size: 1rem;
    }

    .skill-grid {
        grid-template-columns: repeat(2, 1fr);
    }

    #contact form {
        padding: 0 1rem;
    }
}
.resume-info {
    margin-top: 2rem;
    padding: 2rem;
    background-color: #212121;
    border-radius: 10px;
}

.resume-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

.resume-item {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
    border-bottom: 1px solid var(--text-color);
}

.resume-item p:first-child {
    font-weight: bold;
}

/* Update skills section */

.skill-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
}

.skill-item {
    background-color: #212121;
    padding: 1.5rem;
    border-radius: 10px;
    text-align: center;
    transition: transform 0.3s ease;
}

.skill-item:hover {
    transform: translateY(-5px);
}

.skills_box {
    width: 50px;
    height: 50px;
    margin-bottom: 1rem;
}

.skill-item h3 {
    color: var(--accent-color);
    margin-bottom: 0.5rem;
}

/* Responsive design */

@media screen and (max-width: 768px) {
    nav ul {
        flex-direction: row;
        gap: 1rem;
        align-items: center;
    }

    nav ul li {
        margin: 0.5rem 0;
    }

    .resume-grid {
        grid-template-columns: 1fr;
    }

    .skill-grid {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
}

@media screen and (max-width: 480px) {
    .hero-text h1 {
        font-size: 2rem;
    }

    .skill-grid {
        grid-template-columns: 1fr;
    }
}
#home > div
{
    margin-bottom: 10rem;
}


.work-experience {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: Arial, sans-serif;
    padding: 2rem;
}

.work-experience h2 {
    color: var(--accent-color);
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
}

.timeline {
    position: relative;
    border-left: 2px solid var(--secondary-color);
    padding-left: 1.5rem;
}

.experience-item {
    margin-bottom: 2rem;
    position: relative;
}

.experience-item::before {
    content: '';
    position: absolute;
    left: -1.75rem;
    top: 0.5rem;
    width: 1rem;
    height: 1rem;
    background-color: var(--accent-color);
    border-radius: 50%;
}

.experience-card {
    background-color: var(--secondary-color);
    padding: 1rem;
    border-radius: 0.5rem;
}

.experience-period {
    font-size: 1.25rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.experience-title {
    font-size: 1.125rem;
    font-weight: 500;
    color: var(--accent-color);
    margin-bottom: 0.5rem;
}

.experience-description {
    font-size: 0.875rem;
}

@media (max-width: 640px) {
    .work-experience {
        padding: 1rem;
    }

    .work-experience h2 {
        font-size: 1.5rem;
    }

    .experience-period {
        font-size: 1rem;
    }

    .experience-title {
        font-size: 1rem;
    }

    .experience-description {
        font-size: 0.75rem;
    }
}